.calling {
  margin: 4rem 0;
}

.calling_container {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 4.625rem;
  border-radius: 2rem;
}

.calling_container .title-container {
  text-align: center;
  max-width: 678px;
  margin: 0 auto;
}

.calling_container .title-container h2 {
  color: #FCFCFC;
  font-weight: 700;
  font-size: 24px;
  line-height: 34px;
  letter-spacing: 1px;
  margin-bottom: 12px;
}

.calling_container .title-container h3 {
  color: #FCFCFC;
  letter-spacing: 0.75px;
  font-size: 20px;
  line-height: 32px;
  font-weight: 400;
}

.calling_container .button-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 4rem;
  gap: 2rem;
}

@media (min-width: 992px) {
  .calling_container .title-container h2 {
    font-size: 48px;
    line-height: 56px;
  }
}